//-------------------Follow----------------------
.follow-container {
  position: fixed;
  z-index: 10;
  bottom: 1%;
  right: clamp(10rem, 7vw, 11rem);
  display: flex;
  gap: 1.5rem;
  a {
    button {
      svg {
        fill: $color-primary;
        transition: all 0.3s ease-in-out;
        &:hover {
          fill: #cccccc;
          transform: scale(1.1);
          transition: all 0.3s ease-in-out;
        }
      }
    }
  }
}
