//------------------Mixins---------------------
@mixin absolute {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

@mixin color-mod($colors) {
  @each $state, $color in $colors {
    .article__heading--/*colour name goes here*/ {
      color: map-get($color);
    }
  }
}

//------------------Responsive---------------------
@mixin for-desktop-up {
  @media (min-width: map-get($breakpoints, desktop)) {
    @content;
  }
}
@mixin for-tablet-landscape-up {
  @media (min-width: map-get($breakpoints, tablet)) and (max-width: map-get($breakpoints, desktop)) {
    @content;
  }
}
@mixin for-tablet-portrait-up {
  @media (min-width: map-get($breakpoints, phone)) and (max-width: map-get($breakpoints, tablet)) {
    @content;
  }
}
@mixin for-phone-only {
  @media (max-width: map-get($breakpoints, phone)) {
    @content;
  }
}
@mixin responsive-custom-max-size($max-size) {
  @media (max-width: $max-size) {
    @content;
  }
}
