//-------------------VideoCine----------------------
//Main
.main {
  width: 100%;
  height: 100%;
  &__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    //format ratio 19:9
    // padding: 56.25% 0 0 0;
    //format ratio  3:2
    // padding-top: 66.66%;
    iframe {
      background-color: $bg-primary;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100vmax;
      height: 56.25vmax;
      min-width: 100vw;
      min-height: 100vh;
      pointer-events: none;
    }
    &--blur {
      position: absolute;
      z-index: 3;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100vmax;
      height: 39.25vmax;
      min-width: 100vw;
      backdrop-filter: blur(0.5rem);
      transition: 1s ease-in-out;
    }
  }
  .blurFirefox {
    filter: blur(0.5rem);
    transition: 1s ease-in-out;
  }
}
