//-------------------Menu----------------------
.navbar {
  position: absolute;
  z-index: 5;
  top: 50%;
  transform: translate(0, -50%);
  margin-left: clamp(2rem, 6.7vmin, 6rem);
  @include responsive-custom-max-size(835px) {
    margin-left: clamp(2rem, 6.7vmin, 4rem);
  }
  @include responsive-custom-max-size(800px) {
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
  width: clamp(15vmin, 90.3vmin, 72.2rem);
  }
  height: clamp(45vmin, 45vmin, 37.6rem);

  &:hover {
    & ~ .main__wrapper > .main__wrapper--blur {
      opacity: 0;
      transition: 1s ease-in-out;
    }
    & ~ .blurFirefox {
      filter: blur(0);
      transition: 1s ease-in-out;
    }
  }
  &__wrapper {
    &__active-container {
      display: flex;
      flex-direction: column;
      gap: 2rem;

      li:first-child {
        text-transform: uppercase;
      }
      li {
        span {
          display: inline-block;
          font-size: map-get($font-size, big);
          font-family: map-get($font-primary, bold), $font-default;
          white-space: nowrap;
        }

        p {
          max-width: 72.2rem;
          text-align: justify;
          animation: fadeInNav 1s ease-in-out forwards;
          opacity: 0;
          padding: 0.5rem;
        }
      }
    }
    &__return-btn {
      margin-top: 3rem;

      text-align: right;
      animation: fadeInNav 1s ease-in-out forwards;
      opacity: 0;
      transition: 0.3s ease-in-out;
      button {
        svg {
          fill: #fff;
          stroke-width: 0.0798399;
          transition: all 0.3s ease-in-out;
          &:hover {
            fill: #cccccc;
            transform: scale(1.1);
            transition: all 0.3s ease-in-out;
          }
        }
      }
    }
    &__animations {
      display: flex;
      flex-direction: column;
      justify-content: center;

      a {
        display: inline-block;
        font-size: map-get($font-size, big);
        font-family: map-get($font-primary, bold), $font-default;
        text-transform: uppercase;
        white-space: nowrap;
        transition: all 0.3s ease-in-out;
        &:hover {
          color: #cccccc;
          transform: scale(1.05);
          transition: all 0.3s ease-in-out;
        }
      }
      // ---------------------COURT------------------------------
      .active-court {
        animation: fadeInUpNavCourt 1s ease-in-out forwards;
      }
      .active-courts {
        animation: fadeInDownNavCourts 1s ease-in-out forwards;
      }
      // ---------------------COURT REVERSE------------------------------
      .active-court-reverse {
        animation: fadeInUpNavCourt 1s ease-in-out forwards reverse;
      }
      .active-courts-reverse {
        animation: fadeInDownNavCourts 1s ease-in-out forwards reverse;
      }
      // ---------------------DOCS------------------------------
      .active-doc {
        animation: fadeInUpNavDoc 1s ease-in-out forwards;
      }
      .active-docs {
        animation: fadeInDownNavDocs 1s ease-in-out forwards;
      }
      // ---------------------DOCS REVERSE------------------------------
      .active-doc-reverse {
        animation: fadeInUpNavDoc 1s ease-in-out forwards reverse;
      }
      .active-docs-reverse {
        animation: fadeInDownNavDocs 1s ease-in-out forwards reverse;
      }
      // ---------------------CLIPS------------------------------
      .active-clip {
        animation: fadeInUpNavClip 1s ease-in-out forwards;
      }
      .active-clips {
        animation: fadeInUpNavClips 1s ease-in-out forwards;
      }
      // ---------------------CLIPS REVERSE------------------------------
      .active-clip-reverse {
        animation: fadeInUpNavClip 1s ease-in-out forwards reverse;
      }
      .active-clips-reverse {
        animation: fadeInUpNavClips 1s ease-in-out forwards reverse;
      }
      // ---------------------COMS------------------------------
      .active-com {
        animation: fadeInUpNavCom 1s ease-in-out forwards;
      }
      .active-coms {
        animation: fadeInUpNavComs 1s ease-in-out forwards;
      }
      .active-com-down {
        animation: fadeInDownNavCom 1s ease-in-out forwards;
      }
      // ---------------------COMS REVERSE------------------------------
      .active-com-reverse {
        animation: fadeInUpNavCom 1s ease-in-out forwards reverse;
      }
      .active-coms-reverse {
        animation: fadeInUpNavComs 1s ease-in-out forwards reverse;
      }
      .active-com-down-reverse {
        animation: fadeInDownNavCom 1s ease-in-out forwards reverse;
      }
    }
  }
  .active-anim {
    animation: fadeInNav 2s ease-in-out forwards;
    opacity: 0;
  }
}
